body {
  width: 80%;
  margin: 0 auto;
  margin-top: 50px;

  font-family: "Open Sans", sans-serif;
}

.header {
  text-align: center;
}

.row {
  padding-bottom: 50px;
}
button{
  border-width: 0;
  transition: 0.2s;
  background-color: white;
}
.done:hover {
  background-color: #3e8e41;
  color: white;
  border:1px solid green;
  border-color: #3e8e41;
  border-radius: 2px;
  font-size: 15.5px;
}
.undoo:hover{
    background-color: #ecd000;
    color: white;
    border:1px solid #ecd000;
    border-color: #ecd000;
    border-radius: 2px;
    font-size: 15.5px;
}
.del:hover{
  background-color: #ff0000;
  color: white;
  border:1px solid #ff0000;
  border-color: #ff0000;
  border-radius: 2px;
  font-size: 15.5px;
}
